
.App {
  font-family: sans-serif;
  text-align: center;
}

.navbar {
  position: sticky !important;
  background-color: #24252A;
  width: 100%;
  height: 90px;
  left: 0px;
  top: 0px;
  justify-content: center !important;
  align-items: center;
}

.navbar .navbar-brand {
  font-family: 'Poiret One', cursive;
  font-size: 30px;
  line-height: 60px;
  color: #FFFFFF;
  text-decoration: none;
}

.navbar-title {
  font-family: 'Poiret One', cursive;
  font-size: 30px;
  line-height: 60px;
  color: #FFFFFF;
  text-decoration: none;
  margin-right: 150px !important;
}

.navbar-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
 }

.navbar-item {
  margin: 0 100px;
}

.navbar-link {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 60px;
  color: #FFFFFF;
  text-decoration: none;
}

.loginicon {
  position: absolute;
  width: 80px;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
  color: black;
  height: 40px;
  left: 85%;
  top: 10px;
  background:#2bc6ff;
  border-radius: 24px;
  cursor: pointer;
}

.loginicon:hover{
  color: white;
}

.login-form{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.form-box{
  margin-top: 7vh;
  opacity:80%;
  background: black;
  padding-right: 80px;
  padding-left: 80px;
  border-radius: 20px;
  box-shadow: 10px 10px 10px 1px rgba(0, 0, 0, 0.2);
}

.login-text{
  width: 100%;
  color: white;
  border-bottom: 2px solid white;
  line-height: 30px;
  width: 100%;
  font-weight: bolder;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 5vh;
}

label {
  color:white;
  width: 100%;
  text-align: center;
}

.login-box{
  border: 1px solid lightgrey;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  padding: 2px;
  padding-left: 10px;
  letter-spacing: 1px;
  font-weight: 600;
  margin: 5px;
  font-size: 11pt;
  transition: 1s;
  outline: none;
}

.login-btn{
  width: 75%;
  justify-content: center;
  align-items: center;
  background-color: rgb(48,172,251);
  color: white;
  margin-top: 5px;
  border-radius: 20px;
  border: none;
  font-size: 15pt;
  font-weight: 700;
  transition: 1s;
  outline: none;
  cursor: pointer;
  margin-bottom: 20px;
}

.login-btn:hover{
  transform: scale(1.03);
  letter-spacing: 4px;
  background-color: #7FFFD4;
  color: #1E90FF;
}

.show {
  display: block;
  transition: 800ms;
  opacity: 1;
}

.show.active {
  transform: translateY(100px) scale(0) rotateY(180deg);
  opacity: 0;
}

form .signup-link a {
  color: #2bc6ff;
  text-decoration: none;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
}

.contact-info iframe {
                   
  height: 400px;
  flex: 1 100%;
}

